/*ADD SHADOWS OPTIONAL*/
.square-flip .front-to-right .boxshadow,
.square-flip .front-to-right .textshadow,
.square-flip .back-to-left .boxshadow,
.square-flip .back-to-left .textshadow,
.square-flip .front-to-left .boxshadow,
.square-flip .front-to-left .textshadow,
.square-flip .back-to-right .boxshadow,
.square-flip .back-to-right .textshadow {
    -ms-transition: 0.60s;
    transition: 0.60s;
    -webkit-transition: 0.60s;
}

.square-flip .front-to-right .boxshadow, .square-flip .front-to-left .boxshadow {
    -webkit-box-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
    box-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
}

.square-flip .front-to-right .textshadow, .square-flip .front-to-left .textshadow {
    -webkit-text-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
    -moz-text-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
    text-shadow: 24px 42px 58px -8px rgba(0, 0, 0, 0.3);
}

.square-flip:hover .front-to-right .boxshadow,
.square-flip:hover .front-to-right .textshadow,
.square-flip:hover .front-to-left .boxshadow,
.square-flip:hover .front-to-left .textshadow {
    -webkit-box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    -moz-box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
}


.square-flip .back-to-left .boxshadow, .square-flip .back-to-right .boxshadow {
    -webkit-box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    -moz-box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    box-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
}

.square-flip .back-to-left .textshadow, .square-flip .back-to-right .textshadow {
    -webkit-text-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    -moz-text-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
    text-shadow: 240px 42px 58px -8px rgba(0, 0, 0, 0.0);
}

.square-flip:hover .back-to-left .boxshadow,
.square-flip:hover .back-to-left .textshadow,
.square-flip:hover .back-to-right .boxshadow,
.square-flip:hover .back-to-right .textshadow {
