// -----------------------------------------------
// --------------MAIN SETTINGS--------------------
// -----------------------------------------------

//? Миксин сброса стилей
@include reset();

//? Общая обёртка
.full-wrapper {
    position: relative;
    overflow: hidden;
}

//? Размеры контейнеров
.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
    @include xxl(max-width, rem(1410));
    @include xxxl(max-width, rem(1890));
    position: relative;
}

body {
    font-family: $base_ff;
    font-size: $base_fz;
    line-height: 1.5;
    background: rgb(18,22,25);
    background: linear-gradient(90deg, rgba(18,22,25,1) 0%, rgba(18,22,25,1) 50%, rgba(18,22,25,1) 100%);
    overflow-x: hidden;
}

img {
    display: block;
}

a {
    font-family: inherit;
    cursor: pointer;
    color: inherit;
    text-decoration: none;
    transition: color 0.3s ease;

    &:hover {
        color: $base_hover;
        transition: color 0.3s ease;
    }
}

button {
    outline: none;
    border: transparent;
}


